<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <AsideBar />
    <div class="Control_panel">
      <div class="header">
        <div class="content">
          <p>{{ $t("الملف الشخصي") }}</p>
        </div>
        <headerDetails @user-data="handleUserData" />
      </div>
      <div class="profile-card">
        <div class="headerCard">
          <div class="image">
            <img
              :src="
                imagePreview ||
                user.image ||
                require('@/assets/images/profileImage.jpg')
              "
              alt="Profile Picture"
              class="profile-image"
            />
            <input
              id="file-upload"
              type="file"
              accept="image/*"
              @change="handleImageUpload"
              style="opacity: 0; z-index: 2"
              class="inputFile"
            />

            <font-awesome-icon
              :icon="['fas', 'pen-to-square']"
              class="iconEdit"
            />
          </div>
          <div>
            <div class="name">{{ user.name }}</div>
            <div class="email">{{ user.email }}</div>
          </div>
        </div>

        <div class="personal-info">
          <div class="headerContent">
            <div class="section-title">تعديل معلومات شخصية</div>
            <button class="addRestbtn" @click="saveProfile">حفظ</button>
          </div>
          <div class="info">
            <div class="input-container">
              <label class="label">الأسم بالكامل:</label>
              <span class="value">
                <input type="text" v-model="user.name" />
              </span>
            </div>
            <div class="input-container">
              <label class="label">البريد الالكتروني:</label>
              <span class="value">
                <input type="text" v-model="user.email" />
              </span>
            </div>
            <div class="input-container">
              <label class="label">رقم التليفون:</label>
              <span class="value">
                <input type="text" v-model="user.phone" />
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { useToast } from 'vue-toastification';
import AsideBar from "@/components/AsideBar.vue";
import headerDetails from "@/components/dashboard/headerDetails.vue";

export default {
  name: "addBanners",
  setup() {
    // Get toast interface
    const toast = useToast();
    return { toast };
  },
  components: {
    AsideBar,
    headerDetails,
  },
  data() {
    return {
      user: {},
      imagePreview: null,
    };
  },
  computed: {},
  mounted() {
    console.log(this.user);
  },
  methods: {
    handleUserData(userData) {
      this.user = userData;
    },
    handleImageUpload(event) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.imagePreview = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    saveProfile() {
      console.log("saveProfile method called");
      // Simple validation
      if (!this.user.name || !this.user.email || !this.user.phone) {
        this.toast.error(this.$t("Please fill in all fields"));
        return;
      }

      // Email validation
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(this.user.email)) {
        this.toast.error(this.$t("Please enter a valid email address"));
        return;
      }

      // Phone number validation (example: must be numeric and 10 digits)
      const phonePattern = /^\d{10}$/;
      if (!phonePattern.test(this.user.phone)) {
        this.toast.error(this.$t("Please enter a valid phone number (10 digits)"));
        return;
      }

      // If all validations pass
      this.toast.success(this.$t("Profile updated successfully"));
      console.log("Profile data:", this.user);
      // Here you would typically send the data to the server
    },
  },
};
</script>
