                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
            </div>
            <div class="upload-text">
              {{ mode === "add" ? $t("Drag_drop") : $t("uploadImage") }}
            </div>
          </div>
        </div>
        <div class="input-container" v-if="mode === 'view'">
          <label>{{ $t("productImage") }}</label>
          <img
            v-if="formData.cover_image"
            :src="formData.cover_image"
            alt="Product Image"
            class="preview-image"
          />
          <p v-else>{{ $t("NoImage") }}</p>
        </div>
        <div class="popup-actions">
          <button type="submit" v-if="mode !== 'view'">{{ $t("Save") }}</button>
          <span></span>
        </div>
      </form>
      <div v-else>
        <p style="text-align: center">{{ $t("confirmDeleteProduct") }}</p>
        <p style="text-align: center; color: #545454">
          {{ $t("deleteWarning") }}
        </p>
        <div class="popup-actions">
          <button
            style="background-color: #f02116; color: #ffffff"
            @click="deleteRow"
          >
            {{ $t("delete") }}
          </button>
