}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 100%;
  max-width: 559px;
  height: 58px;
  padding: 8px 16px;
  border-radius: 40px;
  border: none;

  font-size: 14px;
  cursor: pointer;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .popup-actions button {
    height: 48px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .popup-actions button {
