    padding: 6px 12px;
  }
}

.popup-actions button:first-child {
  background-color: #5c0e35;
  color: #ffffff;
  font-family: Almarai !important;
}

.popup-actions button:last-child {
  background-color: #ffffff;
  font-family: Almarai !important;
  color: #545454;
}

form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
select {
  width: 99%;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
.input-container.file {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: auto;
}
.upload-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
  border: 2px dashed #ddd;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 10px;
}
.upload-icon {
  font-size: 2rem;
  color: #999;
}
.upload-text {
  text-align: center;
  color: #999;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
}
.preview-image {
  max-width: 25%;
  text-align: center;
  height: auto;
  border-radius: 8px;
}
.load {
  width: 138px;
  height: 32px;
  border-radius: 20px;
  gap: 5px;
  background-color: #5c0e35;
  color: #ffffff;
