<template>
  <div class="home" :dir="$i18n.locale === 'ar' ? 'rtl' : 'ltr'">
    <AsideBar />
    <div class="Control_panel">
      <div class="header">
        <div class="content">
          <p>{{ $t("تغيير كلمه السر") }}</p>
        </div>
        <headerDetails @user-data="handleUserData" />
      </div>
      <div class="profile-card">
        <div class="personal-info">
          <div class="headerContent">
            <div class="section-title">{{ $t("تغيير كلمه السر") }}</div>
            <button class="addRestbtn" @click="submitForm">حفظ</button>
          </div>
          <div class="info">
            <div class="input-container">
              <label class="label">كلمه السر القديمه</label>
              <span class="value">
                <input type="password" v-model="oldPassword" />
              </span>
            </div>
            <div class="input-container">
              <label class="label">كلمه السر الجديده</label>
              <span class="value">
                <input type="text" v-model="newPassword" />
              </span>
            </div>
            <div class="input-container">
              <label class="label">تاكيد كلمه السر </label>
              <span class="value">
                <input type="text" v-model="confirmPassword" />
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import AsideBar from "@/components/AsideBar.vue";
import headerDetails from "@/components/dashboard/headerDetails.vue";
import { useToast } from "vue-toastification";
import useVuelidate from "@vuelidate/core";
import { required, sameAs, minLength } from "@vuelidate/validators";

export default {
  name: "addBanners",
  components: {
    AsideBar,
    headerDetails,
  },
  setup() {
    const toast = useToast();
    return { v$: useVuelidate(), toast };
  },
  data() {
    return {
      oldPassword: "",
      newPassword: "",
      confirmPassword: "",
    };
  },
  validations() {
    return {
      oldPassword: { required },
      newPassword: { 
        required,
        minLength: minLength(6) 
      },
      confirmPassword: {
        required,
        sameAs: sameAs(this.newPassword)
      }
    };
  },
  computed: {},
  mounted() {
    console.log(this.user);
  },
  methods: {
    async submitForm() {
      const isFormValid = await this.v$.$validate();
      
      if (!isFormValid) {
        this.toast.error("Please fill all fields correctly");
        return;
      }

      try {
        // Here you would typically call your API to change password
        // For now we'll just show a success message
        this.toast.success("Password changed successfully!");
        
        // Reset form
        this.oldPassword = "";
        this.newPassword = "";
        this.confirmPassword = "";
        this.v$.$reset();
      } catch (error) {
        this.toast.error("Failed to change password. Please try again.");
      }
    }
  },
};
</script>
