import { createI18n } from "vue-i18n";
import en from "./locales/en.json";
import ar from "./locales/ar.json";

const availableLocales = ["en", "ar"];

function getValidLocale() {
  const locale = localStorage.getItem("lang") || "ar";
  return availableLocales.includes(locale) ? locale : "ar";
}

const i18n = createI18n({
  legacy: false, // Set to false for Vue 3 Composition API support
  locale: getValidLocale(), // Default language
  fallbackLocale: "en",
  messages: {
    en,
    ar,
  },
});

export default i18n;
