<script>
// @ is an alias to /src
import AsideBar from "@/components/AsideBar.vue";
import headerDetails from "@/components/dashboard/headerDetails.vue";
import PopupComponent from "@/components/dashboard/popus/bannersPopus.vue";

export default {
  name: "addBanners",
  components: {
    AsideBar,
    headerDetails,
    PopupComponent,
  },
  data() {
    return {
      filter: "",
      rows: [
        {
          code: 2,
          photo: "",
          bannerName: "لافتة العروض",
          state: true,
          Distinctive: true,
        },
        // More products here
      ],
      popupData: {
        visible: false,
        type: "",
        row: null,
      },
      deletePopup: {
        visible: false,
        row: null,
      },
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          (typeof row.bannerName === "string" &&
            row.bannerName.toLowerCase().includes(searchTerm)) || // Ensure 'bannerName' is a string
          (row.Distinctive &&
            row.Distinctive.toString().toLowerCase().includes(searchTerm)) // Ensure 'Distinctive' exists
        );
      });
    },
  },
  mounted() {
    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
    updateStatus(rows) {
      // Here you would typically make an API call to update the status on the server
      console.log("Updating status for row:", rows);
      // Example:  axios.put(`/api/items/${rows.code}`, { state: rows.state, Distinctive: rows.Distinctive });
    },
    openPopup(type, row) {
      this.popupData = {
        visible: true,
        type,
        row,
      };
    },
    closePopup() {
      this.popupData.visible = false;
    },
    handleSave(updatedRow) {
      if (this.popupData.type === "edit") {
        const index = this.rows.findIndex(
          (row) => row.code === updatedRow.code
        );
        if (index !== -1) {
          this.rows.splice(index, 1, updatedRow);
        }
      } else if (this.popupData.type === "add") {
        updatedRow.code =
          this.rows.length > 0
            ? Math.max(...this.rows.map((row) => row.code)) + 1
            : 1; // Increment code or start from 1 if no rows exist
        this.rows.push(updatedRow);
      }
      this.closePopup();
    },
    deleteRow(row) {
      const index = this.rows.findIndex((r) => r.code === row.code);
      if (index !== -1) {
        this.rows.splice(index, 1);
      }
    },
    confirmDelete(row) {
      const index = this.rows.findIndex((r) => r.code === row.code);
      if (index !== -1) {
        this.rows.splice(index, 1);
      }
      this.closePopup();
    },
  },
};
</script>
