<script>
import { useToast } from "vue-toastification";

export default {
  props: {
    type: String,
    data: Object,
  },
  setup() {
    const toast = useToast();
    return { toast };
  },
  data() {
    return {
      localData: { ...this.data },
    };
  },
  methods: {
    handleDragOver(event) {
      event.preventDefault();
    },
    handleDrop(event) {
      event.preventDefault();
      const file = event.dataTransfer.files[0];
      this.processFile(file);
    },
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    handleImageUpload(event) {
      const file = event.target.files[0];
      this.processFile(file);
    },
    processFile(file) {
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.localData.photo = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    save() {
      // Reset errors
      this.errors = {
        bannerName: false,
        photo: false
      };

      // Validate banner name
      if (!this.localData.bannerName || this.localData.bannerName.trim() === '') {
        this.errors.bannerName = true;
        this.toast.error(this.$t('bannerNameRequired'));
        return;
      }

      // Validate image for add/edit operations
      if ((this.type === 'add' || this.type === 'edit') && !this.localData.photo) {
        this.errors.photo = true;
        this.toast.error(this.$t('bannerImageRequired'));
        return;
      }

      this.$emit("save", this.localData);
      this.toast.success(
        this.type === 'add' 
          ? this.$t('bannerAddedSuccessfully') 
          : this.$t('bannerUpdatedSuccessfully')
      );
    },
    confirmDelete() {
      this.$emit("delete", this.localData);
      this.toast.success(this.$t('bannerDeletedSuccessfully'));
    },
  },
};
</script>
