<script>
// @ is an alias to /src
import AsideBar from "@/components/AsideBar.vue";
import headerDetails from "@/components/dashboard/headerDetails.vue";

export default {
  name: "addBanners",
  components: {
    AsideBar,
    headerDetails,
  },
  data() {
    return {
      filter: "",
      rows: [
        {
          code: 1,
          name: "أحمد محمد",
          productCount: +97450123456,
          lastPurchase: "إدارة الطلبات ",
        },
        {
          code: 2,
          name: "أحمد محمد",
          productCount: +97450123456,
          lastPurchase: "إدارة الطلبات ",
        },
        // More products here
      ],
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          row.name.toLowerCase().includes(searchTerm) ||
          (row.description &&
            row.description.toLowerCase().includes(searchTerm)) // Ensure 'description' exists
        );
      });
    },
  },
  mounted() {
    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },
  },
};
</script>
