              fill="#FBB040"
            />
          </svg>
          <h3 v-if="type === 'view'">{{ $t("OrderDetails") }}</h3>
          <h3 v-if="type === 'add'">{{ $t("AddOrder") }}</h3>
          <h3 v-if="type === 'edit'">{{ $t("EditOrder") }}</h3>
          <h3 v-if="type === 'delete'">{{ $t("DeleteOrder") }}</h3>
        </div>
        <button class="close-button" @click="$emit('close')">X</button>
      </div>
      <div class="form" v-if="type !== 'delete'">
        <div class="input-container">
          <label>{{ $t("OrderNumber") }}</label>
          <input v-model="formData.id" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("CustomerName") }}</label>
          <input v-model="formData.customerName" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("Phone") }}</label>
          <input v-model="formData.phone" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("orderDetails") }}</label>
          <input
            type="text"
            v-model="orderDetailInput"
            :disabled="type === 'view'"
            @keyup.enter="addOrderDetail"
            :placeholder="$t('AddorderpressEnter')"
          />
        </div>
        <ul>
          <li
            v-for="(detail, index) in formData.address_description"
            :key="index"
          >
            {{ detail }}
            <button v-if="type !== 'view'" @click="removeOrderDetail(index)">
              ×
            </button>
          </li>
        </ul>
        <div class="input-container">
          <label>{{ $t("TotalPrice") }}</label>
          <input v-model="formData.total_price" :disabled="type === 'view'" />
        </div>
        <div class="input-container">
          <label>{{ $t("RestaurantName") }}</label>
          <input
            v-model="formData.restaurant_branch"
            :disabled="type === 'view'"
          />
        </div>
        <div class="input-container">
          <label>{{ $t("OrderStatus") }}</label>
          <select v-model="formData.status" :disabled="type === 'view'">
            <option v-for="status in statuses" :key="status" :value="status">
              {{ status }}
            </option>
          </select>
        </div>
        <div class="input-container">
          <label>{{ $t("OrderDate") }}</label>
          <input
            type="date"
            v-model="formData.created_at"
            :disabled="type === 'view'"
          />
        </div>
      </div>

      <p style="text-align: center; color: black" v-if="type === 'delete'">
        {{ $t("confirmDeleteOrder") }}
      </p>
      <p style="text-align: center" v-if="type === 'delete'">
        {{ $t("deleteWarning") }}
      </p>

      <div class="popup-actions">
        <button
          style="background-color: #f02116"
          v-if="type === 'delete'"
          @click="deleteOrder"
        >
          {{ $t("delete") }}
        </button>
        <button v-if="type === 'add' || type === 'edit'" @click="saveOrder">
          {{ $t("Save") }}
        </button>
        <button @click="$emit('close')">{{ $t("Cancel") }}</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: String,
    data: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      formData: {
        ...this.data,
        address_description: Array.isArray(this.data.address_description)
          ? this.data.address_description
          : this.data.address_description
          ? [this.data.address_description] // Convert to array if it's a string
          : [], // Default to an empty array
        status: this.data.status || "new", // Ensure status has a default value
      },
      orderDetailInput: "",
      statuses: ["new", "waiting", "completed", "processing"], // Ensure statuses are defined
    };
  },
  watch: {
    data: {
      immediate: true,
      handler(newData) {
        this.formData = {
          ...newData,
          address_description: Array.isArray(newData.address_description)
            ? newData.address_description
            : newData.address_description
            ? [newData.address_description] // Convert to array if it's a string
            : [], // Default to an empty array
          status: newData.status || "جديد", // Ensure status has a default value
        };
      },
    },
  },
  methods: {
    addOrderDetail() {
      if (this.orderDetailInput.trim()) {
        this.formData.address_description.push(this.orderDetailInput.trim());
        this.orderDetailInput = "";
      }
    },
    removeOrderDetail(index) {
      this.formData.address_description.splice(index, 1);
    },
    saveOrder() {
      this.$emit("save", this.formData);
    },
    deleteOrder() {
      this.$emit("delete", this.formData.id);
    },
  },
};
</script>

<style scoped>
