  background: #ffffff;
  border-radius: 16px;
  width: 610px;
  max-width: 90%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f5f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.semi-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0 20px 0 20px;
}

.close-button {
  background: none;
  font-family: Almarai !important;
  border: none;
  color: #292d32;
  font-size: 25px;
