  cursor: pointer;
  transition: color 0.3s;
}

.popup-actions {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 12px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.popup-actions button {
  width: 559px;
  height: 58px;
  padding: 8px 16px;
  border: none;

  font-family: Almarai !important;
  border-radius: 40px;
  font-size: 14px;
  cursor: pointer;
}

.popup-actions button:first-child {
  background-color: #5c0e35;
  font-family: Almarai !important;
  color: #ffffff;
}
@media (max-width: 768px) {
  .popup-actions button {
    width: 100%;
    height: auto;
    min-height: 48px;
    padding: 12px 16px;
  }

  .popup-actions {
    width: 100%;
  }
}

.popup-actions button:last-child {
  background-color: #ffffff;
  color: #545454;
  font-family: Almarai !important;
}

ul {
  list-style: none;
  display: flex;
  justify-content: start;
  align-items: center;
}

li {
  background-color: #5c0e35;
  height: 35px;
  border-radius: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px 0 15px;
  color: #ffffff;
}

li button {
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  font-family: Almarai !important;
  color: #5c0e35;
  background-color: #ffffff;
  padding: 2px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 6px;
  transition: color 0.3s;
  margin: 5px;
}
.form {
  margin: 25px 10px 25px 10px;
}

input,
select,
textarea {
  width: 95%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.3s;
}
select {
  width: 99%;
}
.input-container {
  position: relative;
  margin: 10px 0 10px 0;
}
label {
  position: absolute;
  top: -8px;
  background-color: #ffffff;
  font-size: 12px;
  color: #999;
  pointer-events: none; /* Make label not interfere with input clicks */
