<script>
// @ is an alias to /src
import AsideBar from "@/components/AsideBar.vue";
import boxDetails from "@/components/dashboard/boxDetails.vue";
import headerDetails from "@/components/dashboard/headerDetails.vue";
import orderPopus from "@/components/dashboard/popus/orderPopus.vue";
import axios from "axios";

export default {
  name: "OrderManagement",
  components: {
    AsideBar,
    boxDetails,
    headerDetails,
    orderPopus,
  },
  data() {
    return {
      filter: "",
      rows: [
        // More products here
      ],
      isPopupVisible: false,
      popupType: "",
      selectedRow: null,
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          (typeof row.customerName === "string" &&
            row.customerName.toLowerCase().includes(searchTerm)) ||
          (row.orderNumber &&
            row.orderNumber.toString().toLowerCase().includes(searchTerm)) // Ensure 'orderNumber' is defined
        );
      });
    },
  },
  mounted() {
    this.FetchRows(); // Fetch rows dynamically
    this.fetchUserData(); // Fetch user data dynamically
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    fetchUserData() {
      // Simulate fetching user data from an API or store
    },

    FetchRows() {
      const token = localStorage.getItem("authToken");
      axios
        .get("https://backend.alaqdk.com/api/admin/dashboard/get-alll-orders", {
          headers: {
            "Accept-Language": "ar",
            Authorization: `Bearer ${token}`, // Add the Authorization header
          },
        })
        .then((response) => {
          console.log("order Response:", response.data); // Log the response
          const data = response.data;
          if (data.status) {
            this.rows = data.data.map((order) => ({
              id: order.id,
              customerName: order.user?.name || "N/A",
              phone: order.phone || "N/A",
              address_description: order.address_description || "N/A",
              total_price: order.total_price || "N/A",
              restaurant_branch: order.restaurant_branch?.name || "N/A",
              status: order.status || "N/A",
              created_at: order.created_at || "N/A",
            }));
            console.log(this.rows);
          } else {
            console.error("Failed to fetch rows data:", data.message);
          }
        })
        .catch((error) => {
          console.error("Error fetching rows data:", error);
        });
    },
    openPopup(type, row = null) {
      this.popupType = type;
      this.selectedRow = row
        ? { ...row } // Clone the row to avoid direct mutation
        : {
            orderNumber:
              this.rows.length > 0
                ? Math.max(...this.rows.map((r) => r.id)) + 1
                : 1,
            customerName: "",
            phone: "",
            address_description: [],
            total_price: "",
            restaurant_branch: "",
            status: "جديد", // Ensure status has a default value
            created_at: new Date().toISOString().split("T")[0],
          };
      this.isPopupVisible = true;
    },
    closePopup() {
      this.isPopupVisible = false;
      this.popupType = "";
      this.selectedRow = null;
    },
    handleSave(updatedRow) {
      if (this.popupType === "add") {
        this.rows.push(updatedRow);
      } else if (this.popupType === "edit") {
        const index = this.rows.findIndex((row) => row.id === updatedRow.id);
        if (index !== -1) this.rows.splice(index, 1, updatedRow);
      }
      this.closePopup();
    },
    handleDelete(orderNumber) {
      this.rows = this.rows.filter((row) => row.id !== orderNumber);
      this.closePopup();
    },
  },
};
</script>
