      <div class="header">
        <div class="content">
          <p>{{ $t("السياسه والخصوصيه") }}</p>
        </div>
        <headerDetails @user-data="handleUserData" />
      </div>
      <div class="container">
        <div class="form-group">
          <label for="description">الوصف</label>
          <div
            class="text-area"
            contenteditable="true"
            @input="updateContent"
            :style="{
              textAlign: textAlign,
              fontWeight: isBold ? 'bold' : 'normal',
              fontStyle: isItalic ? 'italic' : 'normal',
            }"
          >
            {{ content }}
          </div>        </div>
        <div class="form-group">
          <label for="content">المحتوى</label>
          <div
            class="text-area"
            contenteditable="true"
