</template>

<script>
// @ is an alias to /src
import AsideBar from "@/components/AsideBar.vue";
import headerDetails from "@/components/dashboard/headerDetails.vue";

export default {
  name: "addBanners",
  components: {
    AsideBar,
    headerDetails,
  },
  data() {
    return {
      description:
        "نحن في على قدك نقدر خصوصيتك وتسعى لحماية معلوماتك الشخصية. توضح هذه السياسة كيفية جمعنا واستخدامنا وحمايتنا لمعلوماتك، بالإضافة إلى حقوقك المتعلقة بهذه البيانات.",
