        "نحن في على قدك نقدر خصوصيتك ونسعى لحماية معلوماتك الشخصية. توضح هذه السياسة كيفية جمعنا واستخدامنا وحمايتنا لمعلوماتك، بالإضافة إلى حقوقك المتعلقة بهذه البيانات ...... I",
      isBold: false,
      isItalic: false,
      textAlign: "right",
    };
  },
  computed: {},
  mounted() {},
  methods: {
    updateContent(event) {
      this.content = event.target.textContent;
    },
    alignText(alignment) {
      this.textAlign = alignment;
    },
    toggleBold() {
      this.isBold = !this.isBold;
    },
    toggleItalic() {
      this.isItalic = !this.isItalic;
    },
    resetFields() {
      this.content = "";
    },
    saveData() {
      alert("Saving data!");
    },
  },
};
</script>
