<script>
// @ is an alias to /src
import AsideBar from "@/components/AsideBar.vue";
import boxDetails from "@/components/dashboard/boxDetails.vue";
import headerDetails from "@/components/dashboard/headerDetails.vue";
import ratingPopup from "@/components/dashboard/popus/ratingPopup.vue";

export default {
  name: "RatingS",
  components: {
    AsideBar,
    boxDetails,
    headerDetails,
    ratingPopup,
  },
  data() {
    return {
      filter: "",
      rows: [
        {
          customerName: "احمد",
          rating: "4.7",
          comment: "جيد",
          ResidentProducer: "مانجا",
          date: "15 أبريل 2025",
        },
        // More products here
      ],
      showPopup: false,
      popupType: "",
      popupData: null,
    };
  },
  computed: {
    filteredRows() {
      return this.rows.filter((row) => {
        const searchTerm = this.filter.toLowerCase();
        return (
          row.customerName.toLowerCase().includes(searchTerm) || // Correct property name
          (row.comment && row.comment.toLowerCase().includes(searchTerm)) || // Ensure 'comment' exists
          (row.ResidentProducer &&
            row.ResidentProducer.toLowerCase().includes(searchTerm)) || // Ensure 'ResidentProducer' exists
          (row.date && row.date.toLowerCase().includes(searchTerm)) // Ensure 'date' exists
        );
      });
    },
  },
  mounted() {
    if (this.$toast) {
      this.$toast.success("Welcome to your dashboard!");
    }
  },
  methods: {
    openPopup(type, data = null) {
      this.popupType = type;
      this.popupData = data;
      this.showPopup = true;
    },

    closePopup() {
      this.showPopup = false;
      this.popupType = "";
      this.popupData = null;
    },
    handleSave() {
      if (this.popupType === "delete") {
        this.rows = this.rows.filter(
          (row) => row.customerName !== this.popupData.customerName
        );
      }
      this.closePopup();
    },
  },
};
</script>
